










































	import header from "../../../components/header.vue"
	export default { 
		components:{
			headers:header
		},
		data(){
			return {
				imgSrc:"../../../static/img/back.png",
				title:"手机快捷登陆",
				user:"",
				pass:""
				// imgSrc:imgs
			}
		},
		computed:{
			isDisabled(){
				if(this.user.length == 11){
					return true;
				}else{
					return false;
				}
			}
		},
		methods:{
			back(){
				uni.navigateBack();
			},
			goInfo(){
				uni.navigateTo({
				    url: '/pages/page/info/info'
				});
			},
			sendyzm(){
				if(!this.vali.is_phone(this.user)){
					uni.showToast({
						icon:"none",
						title: '手机号码格式不正确且只能输入手机号',
						duration: 2000
					});
					this.user = "";
					return false;
				}
				this.yzmTo();
			},
			yzmTo(){
				
				uni.request({
					url: this.userUrl + "/BtCApi/Login/LoginSms", 
					method:'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					data: {
						Mob: this.user,
						type: 1,
						DeviceId: "",
						PhoneVersion: "",
						ClientVersion: "",
						ClientType: 0,
					},
					success: (res) => {
						console.log(res.data);
						uni.showToast({
							icon:"none",
							title: res.data.msg,
							duration: 2000
						});
					}
				});
			},
			isLogin(){
				if (!this.vali.is_phone(this.user)) {
                        uni.showToast({
							icon:"none",
                        	title: '手机号码格式不正确',
                        	duration: 2000
                        });
						 return false;
                     }

                     
					 this.login();
			},
			login(){
				const _this = this;
				uni.request({
					url: this.userUrl + "/BtCApi/Login/APP_LoginByMob", //仅为示例，并非真实接口地址。
					method:'POST',
					 header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					data: {
						Verification_Code: this.pass,
						Mob: this.user,
						type:1,
						DeviceId: '',
						PhoneVersion: null,
						ClientVersion: '',
						ClientType: 1,
					},
					success: (res) => {
						console.log(res.data);
						if(res.data.status == true){
							uni.showToast({
								icon:"none",
								title: "登陆成功",
								duration: 2000
							});
							uni.setStorageSync('UserID', res.data.UserID);
							uni.setStorageSync('Signid', res.data.Signid);
							uni.setStorageSync('DeviceId', res.data.DeviceId);
							uni.setStorageSync('PhoneVersion', res.data.PhoneVersion);
							uni.setStorageSync('ClientVersion', res.data.ClientVersion);
							uni.setStorageSync('ClientType', res.data.ClientType);
							uni.switchTab({
								url: '/pages/router/mine/mine'
							});
						}else{
							uni.showToast({
								icon:"none",
								title: res.data.msg,
								duration: 2000
							});
						}
						
					}
				});
			}
		}
	}
